.profile__name {
  font-weight: 500;
  font-size: 42px;
  margin: 0;
  max-width: 294px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width:700px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    max-width: 250px;
  }
}
